<script>
  import { onMount } from "svelte";
  import {jwtToken} from "./store";
  let recordData = [];
  let loading = true;

  //    
  async function fetchGameData() {
    try {
      const response = await fetch("/no_approved_record", {
        method: "GET",
        headers: { "Content-Type": "application/json", "d2rpvpjwttoken": $jwtToken }
      });

      if (!response.ok) throw new Error(` : ${response.status}`);
      recordData = await response.json();
      loading = false;
    } catch (error) {
      console.error("  :", error.message);
      alert("    ");
    }
  }

  //  /  
  async function handleRecord(action, orderNum) {
    const endpoint = action === "approve" ? "/approve-record" : "/delete-record";
    const data = { orderNum };

    try {
      const response = await fetch(endpoint, {
        method: "POST",
        headers: { "Content-Type": "application/json", "d2rpvpjwttoken": $jwtToken },
        body: JSON.stringify(data)
      });

      if (response.ok) {
        alert(`${action === "approve" ? "" : ""} `);
        fetchGameData();
      } else {
        throw new Error(` : ${response.status}`);
      }
    } catch (error) {
      console.error(`${action === "approve" ? "" : ""} :`, error.message);
      alert(` : ${error.message}`);
    }
  }

  //      
  onMount(fetchGameData);
</script>

<table>
  <tr>
    <th class="record-table-date"></th>
    <th class="record-table-loser"></th>
    <th class="record-table-score"></th>
    <th class="record-table-ok"></th>
  </tr>
  {#if loading}
    <tr><td colspan="4" class="text-center"> ...</td></tr>
  {:else if recordData.length === 0}
    <tr><td colspan="4" class="text-center"> </td></tr>
  {:else}
    {#each recordData as row}
      <tr>
        <td>{new Date(row.Date).toLocaleDateString()}</td>
        <td>{row.Loser}</td>
        <td>{row.LScore}</td>
        <td>
          <button on:click={() => handleRecord("approve", row.OrderNum)}></button>
          <button on:click={() => handleRecord("delete", row.OrderNum)}></button>
        </td>
      </tr>
    {/each}
  {/if}
</table>

<style>
  table {
    width: 90%;
    margin-top: 20px;
  }
  .record-table-date {
    width: 15%;
  }
  .record-table-loser {
    width: 40%;
  }
  .record-table-score {
    width: 15%;
  }
  .record-table-ok {
    width: 20%;
  }
</style>
