<script>
    import { clickformopen, fetechClanList, clanlists, jwtToken } from "./store.js";
    import { onMount } from "svelte";

    let clan = "none";

    onMount(async () => {
        await fetechClanList();
    });


    async function clanjoin() {
    try {
        //    ( )
        if (clan === "none") {
            alert("  .");
            return;
        }
        const data = { clanname: clan };
        
        const response = await fetch('/clanjoin', {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "d2rpvpjwttoken": $jwtToken
            },
            body: JSON.stringify(data),
        });

        if (response.ok) {
            alert("  ");
            clickformopen(null);
        } else {
            const errorData = await response.json(); //     
            alert(` : ${response.status}, : ${errorData.message || ' '}`);
        }
    } catch (error) {
        alert(`    : ${error.message}`);
    }
}

</script>

<div class="left">
    <select class="namewidth" bind:value={clan}>
        <option value="none"> </option>
        {#each $clanlists as list}
            <option value={list}>{list}</option>
        {/each}
    </select>

    <button on:click={() => clanjoin()}></button>
</div>
