<script>
  import {
    mode,
    clickformopen, jwtToken
  } from "./store.js";
  import { onMount } from "svelte";

  let oldpw = "";
  let newpw = "";
  let checkpw = "";

  onMount(() => {});

  async function changepw() {
    if (newpw !== checkpw) {
      alert("   .  .");
      return;
    }

    const pwdata = {
      nowpw: oldpw,
      newpw: newpw,
    };

    try {
      const endpoint = $mode ? "/process_changepw_m" : "/process_changepw";
      const response = await fetch(endpoint, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "d2rpvpjwtToken": $jwtToken
        },
        body: JSON.stringify(pwdata),
      });

      if (response.ok) {
        alert("  ");
        clickformopen(null);
      } else {
        alert(` : ${response.status}`);
      }
    } catch (error) {
      console.error(" :", error);
      alert(" : " + error.message);
    }
  }
</script>

<div class="left">
  
  <input class="namewidth" type="password" bind:value={oldpw} />
   
  <input class="namewidth" type="password" bind:value={newpw} />
   
  <input class="namewidth" type="password" bind:value={checkpw} />
  <button on:click={() => changepw()}> </button>
</div>
