<script>
  import {
    clickformopen,
    mode,
    nickname,
    jwtToken
  } from "./store.js";

  let loginnickname = "";
  let loginpw = "";


//    ( )
async function formprocess() {
  let logindata = { nickname: loginnickname, password: loginpw };
  try {
    const endpoint = $mode ? "/process_login_m" : "/process_login";
    const response = await fetch(endpoint, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(logindata),
    });

    if (response.status === 200) {
      const data = await response.json();
      
      // JWT   
      const jwtTokenFromHeader = response.headers.get('d2rpvpjwttoken');
      if (jwtTokenFromHeader) {
        jwtToken.set(jwtTokenFromHeader); // JWT  
      }
      nickname.set(data.username); //     
      clickformopen(null);
    } else {
      alert(" : " + (await response.text())); //   
    }
  } catch (error) {
    alert(error);
  }
}
</script>

<div class="left">
  
  <input class="namewidth" type="text" bind:value={loginnickname} />
  
  <input class="namewidth" type="password" bind:value={loginpw} />
  <button class="posit1" on:click={() => formprocess()}></button>
  <button class="posit2" on:click={() => clickformopen("findpw")}
    ></button
  >
</div>

<style>
  .posit1 {
    position: relative;
    top: -35px;
    left: 280px;
  }
  .posit2 {
    position: relative;
    left: -70px;
  }
</style>
