<script>
  import { onMount } from "svelte";
  import { key } from "../store.js";
  let rankData = [];
  let loading = true;
  let error = null;
  let showDetails = [];

  async function fetchData() {
    try {
      const response = await fetch("/rankdata_m");
      if (!response.ok) {
        throw new Error(" ");
      }
      rankData = await response.json();

    } catch (err) {
      error = err;
    } finally {
      loading = false;
    }
  }

  //     
  onMount(() => {
    const unsubscribe = key.subscribe((value) => {
      fetchData();
    });

    //     
    return () => {
      unsubscribe();
    };
  });

  function toggleDetails(index) {
    showDetails[index] = !showDetails[index];
    showDetails = [...showDetails]; //   Svelte  
  }

  function handleKeyDown(event, index) {
    if (event.key === "Enter" || event.key === " ") {
      toggleDetails(index);
    }
  }
</script>
<svelte:head>
    <title>mpk </title>
</svelte:head>

<!-- <div class="main_data">
  <select
    class="namewidth center"
    bind:value={selectfilter}
    on:change={() => filterData()}
  >
    <option value="0">  </option>
    <option value="1">   </option>
    <option value="10"> 10   </option>
  </select>
</div> -->
<div class="table-outline main_data rank">
  <div class="table-head">
    <div class="table-head_rank table-contents_cell"></div>
    <div class="table-head_nickname table-contents_cell"></div>
    <div class="table-head_winrate table-contents_cell"></div>
    <div class="table-head_score table-contents_right-cell"></div>
  </div>
  {#if loading}
    <p> ...</p>
  {:else if error}
    <p>Error: {error.message}</p>
  {:else}
    {#each rankData as user, index}
      <div class="table-contents-wrapper">
        <div
          class="table-contents"
          on:click={() => toggleDetails(index)}
          on:keydown={handleKeyDown}
        >
          <div class="table-contents_rank table-contents_cell">
            {#if user.rank / rankData.length <= 0.1}<img
                class="ranksize"
                src="/img/diamond.png"
                alt="10%"
              />
            {:else if user.rank / rankData.length <= 0.25}<img
                class="ranksize"
                src="/img/platinum.png"
                alt="25%"
              />
            {:else if user.rank / rankData.length <= 0.45}<img
                class="ranksize"
                src="/img/gold.png"
                alt="45%"
              />
            {:else if user.rank / rankData.length <= 0.7}<img
                class="ranksize"
                src="/img/silver.png"
                alt="70%"
              />
            {:else}<img class="ranksize" src="/img/bronze.png" alt="100%" />
            {/if}
          </div>
          <!--  () -->
          <div class="table-contents_nickname table-contents_cell">
            {user.nickname.replace("_m", "")}
          </div>
          <!--  -->
          <div class="table-contents_winrate table-contents_cell">
            {user.clan==="none" ? "" : user.clan}
          </div>
          <!--  ( ) -->
          <div class="table-contents_score table-contents_right-cell">
            {user.LScore}
          </div>
          <!--  (BScore + LScore) -->
        </div>
        {#if showDetails[index]}
          <div class="table-contents_detail show">
         
          </div>
        {/if}
      </div>
    {/each}
  {/if}
</div>

<style>
  .table-head_rank,
  .table-contents_rank {
    width: 15%;
  }

  .table-head_nickname,
  .table-contents_nickname {
    width: 40%;
  }

  .table-head_winrate,
  .table-contents_winrate {
    width: 25%;
  }

  .table-head_score,
  .table-contents_score {
    width: 20%;
  }
  .table-contents:hover {
text-decoration: underline;

  }

</style>
