<script>
  import { mode, clickformopen, nickname, jwtToken } from "./store.js";

  let pw = "";

  //    
  async function delete_account() {
    const pwdata = {
      nowpw: pw,
    };

    try {
      const endpoint = $mode ? "/delete_account_m" : "/delete_account";
      const response = await fetch(endpoint, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          "d2rpvpjwttoken": $jwtToken,
        },
        body: JSON.stringify(pwdata),
      });

      if (response.ok) {
        alert("  ");
        clickformopen(null);
        nickname.set(null);
        jwtToken.set(null);
      } else {
        alert(` : ${response.status}`);
      }
    } catch (error) {
      console.error(" :", error);
      alert(" : " + error.message);
    }
  }
</script>
  
  <div class="left">
      
    <input class="namewidth" type="password" bind:value={pw} />
    <button on:click={() => delete_account()}> </button>
  </div>
  