<script>
  import { jwtToken } from "./store";
    import { onMount } from "svelte";
    let recordData = [];
    let loading = true;
  
    //    
    async function fetchGameData() {
      try {
        const response = await fetch("/no_approved_clan_record", {
          method: "GET",
          headers: { "Content-Type": "application/json",
            "d2rpvpjwttoken": jwtToken,
           }
        });
  
        if (!response.ok) throw new Error(` : ${response.status}`);
        recordData = await response.json();
        loading = false;
      } catch (error) {
        console.error("  :", error.message);
        alert("    ");
      }
    }
  
    //  /  
    async function handleRecord(action, orderNum, result) {
      const endpoint = action === "approve" ? "/approve-clan-record" : "/delete-clan-record";
      const data = { orderNum: orderNum,
        result: result
       };
  
      try {
        const response = await fetch(endpoint, {
          method: "POST",
          headers: { "Content-Type": "application/json","d2rpvpjwttoken": $jwtToken },
          body: JSON.stringify(data)
        });
  
        if (response.ok) {
          alert(`${action === "approve" ? "" : ""} `);
          fetchGameData();
        } else {
          throw new Error(` : ${response.status}`);
        }
      } catch (error) {
        console.error(`${action === "approve" ? "" : ""} :`, error.message);
        alert(` : ${error.message}`);
      }
    }
  
    //      
    onMount(fetchGameData);
  </script>
  
  <table>
    <tr>
      <th class="record-table-date"></th>
      <th class="record-table-loser"></th>
      <th class="record-table-score"></th>
      <th class="record-table-ok"></th>
    </tr>
    {#if loading}
      <tr><td colspan="4" class="text-center"> ...</td></tr>
    {:else if recordData.length === 0}
      <tr><td colspan="4" class="text-center"> </td></tr>
    {:else}
      {#each recordData as row}
        <tr>
          <td>{new Date(row.gameDate).toLocaleDateString()}</td>
          <td>{row.loser.replace("_m","")}</td>
          <td>{row.result}</td>
          <td>
            <button on:click={() => handleRecord("approve", row.OrderNum, row.result)}></button>
            <button on:click={() => handleRecord("delete", row.OrderNum, row.result)}></button>
          </td>
        </tr>
      {/each}
    {/if}
  </table>
  
  <style>
    table {
      width: 90%;
      margin-top: 20px;
    }
    .record-table-date {
      width: 15%;
    }
    .record-table-loser {
      width: 40%;
    }
    .record-table-score {
      width: 15%;
    }
    .record-table-ok {
      width: 20%;
    }
  </style>
  