<script>
  import {
    mode,
    clickformopen, jwtToken
  } from "./store.js";

  let pw = "";
  let newemail = "";

  //    
  async function changeemail() {
    const emaildata = {
      nowpw: pw,
      newemail: newemail,
    };

    try {
      const endpoint = $mode
        ? "/process_changeemail_m"
        : "/process_changeemail";
      const response = await fetch(endpoint, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "d2rpvpjwttoken": $jwtToken
        },
        body: JSON.stringify(emaildata),
      });

      if (response.ok) {
        alert("  ");
        clickformopen(null);
      } else {
        alert(` : ${response.status}`);
      }
    } catch (error) {
      console.error(" :", error);
      alert(" : " + error.message);
    }
  }
</script>


<div class="left">
  
  <input class="namewidth" type="password" bind:value={pw} />
   
  <input class="namewidth" type="text" bind:value={newemail} />
  <button on:click={() => changeemail()}> </button>
</div>
