<script>
  import { onMount } from "svelte";
  import { nicknames, fetchNicknames, nickname, mode, jwtToken } from "./store.js";

  let nicknameexceptme = [];
  let myScore = 0;
  let winner;
  let winnerScore = 5;

  //         
  onMount(() => {
    fetchNicknames($mode);
    nicknameexceptme = $nicknames.filter((item) => item !== $nickname);
  });

  //      
  function validateInput(value, options) {
    return options.includes(value) ? value : null;
  }

  //   
  function handle_winner_change(event) {
    winner = validateInput(event.target.value, nicknameexceptme);
  }

  //   
  async function submitrecord() {
    if (myScore > 4 || myScore < 0) {
      alert("  ");
      return;
    }

    const data = { winner, winnerScore, myScore };

    try {
      const response = await fetch("/submitrecord", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "d2rpvpjwttoken": $jwtToken
        },
        body: JSON.stringify(data),
      });

      if (response.ok) {
        alert("  ");
      } else {
        alert(` : ${response.status}`);
      }
    } catch (error) {
      console.error(" :", error);
      alert(" : " + error.message);
    }
  }
</script>

<div class="left">
   
  <input
    list="nicknames_list"
    bind:value={winner}
    on:blur={handle_winner_change}
    class="namewidth"
    placeholder=" "
  />
  <datalist id="nicknames_list">
    {#each nicknameexceptme as option}
      <option value={option}>{option}</option>
    {/each}
  </datalist>

    
  <input type="number" class="scorewidth" bind:value={myScore} min="0" max="4" />
  <button on:click={submitrecord}></button>
</div>
