<script>
  import { onMount } from "svelte";
  import { nicknames, fetchNicknames, nickname, mode, jwtToken } from "./store.js";

  let nicknameexceptme = [];
  let winner;
  let result = true;

  //         
  onMount(async () => {
   await fetchNicknames($mode);
    nicknameexceptme = $nicknames
      .filter((item) => item !== $nickname)
      .map((item) => item.replace(/_m$/, "")); // "_m" 
  });

  //      
  function validateInput(value, options) {
    return options.includes(value) ? value : null;
  }

  //   
  function handle_winner_change(event) {
    winner = validateInput(event.target.value, nicknameexceptme);
  }

  //   
  async function submitrecord() {
    const data = { winner: winner + "_m", result: result };

    try {
      const response = await fetch("/submit_clan_record", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "d2rpvpjwttoken": $jwtToken
        },
        body: JSON.stringify(data),
      });

      if (response.status===200) {
        alert("  ");
      } else if (response.status===406) {
        alert(`      `);
      }
    } catch (error) {
      console.error(" :", error);
      alert(" : " + error.message);
    }
  }

  function changeresult() {
    result = !result;
  }
</script>

<div class="left">
 
<input
  list="nicknames_list"
  bind:value={winner}
  on:blur={handle_winner_change}
  class="namewidth"
  placeholder=" "
/>
<datalist id="nicknames_list">
  {#each nicknameexceptme as option}
    <option value={option}>{option}</option>
  {/each}
</datalist>
<input type="checkbox" on:click={() => changeresult()} /><br/>

<button on:click={submitrecord}></button>
</div>
